<template><section><html><head></head><body><h1>CorridorGeometry、CorridorOutlineGeometry</h1>
<ul>
<li><code v-pre="">vc-geometry-corridor</code> 组件用于加载廊体。</li>
<li><code v-pre="">vc-geometry-outline-corridor</code> 组件用于加载廊体轮廓。</li>
<li>需要作为 <code v-pre="">vc-instance-geometry</code> 的子组件使用，可以挂载到 <code v-pre="">vc-primitive</code> 或 <code v-pre="">vc-primitive-ground</code>。</li>
</ul>
<h2>示例</h2>
<h3>加载廊体</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" @left_click="LEFT_CLICK">
        <vc-primitive-ground :appearance="appearance">
          <vc-instance-geometry :geometry.sync="geometry" :attributes="attributes">
            <vc-geometry-corridor ref="corridor" :positions="positions" :width="width"></vc-geometry-corridor>
          </vc-instance-geometry>
        </vc-primitive-ground>
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :attributes="attributesOutline">
            <vc-geometry-outline-corridor ref="corridorOutline" :positions="positionsOutline" :width="width"></vc-geometry-outline-corridor>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
