<h2>属性</h2>
<h3><code v-pre="">vc-geometry-corridor</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre="">required</code> 指定描述 corridor 位置的经纬度(高度)数组。 <strong>结构：[{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>width</td>
<td>Number</td>
<td></td>
<td><code v-pre="">required</code> 指定 corridor 边之间的距离。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 corridor 参考椭球体。</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定每个经纬度之间的采样粒度。</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 corridor 高度。</td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 corridor 拉伸高度。</td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 corridor 要缓存的顶点属性。</td>
</tr>
<tr>
<td>cornerType</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 corridor 转角样式。<strong>ROUNDED: 0, MITERED: 1, BEVELED: 2</strong></td>
</tr>
</tbody>
</table>
<hr>
<h3><code v-pre="">vc-geometry-outline-corridor</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre="">required</code> 指定描述 corridor 位置的经纬度(高度)数组。 <strong>结构：[{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>width</td>
<td>Number</td>
<td></td>
<td><code v-pre="">required</code> 指定 corridor 边之间的距离。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 corridor 参考椭球体。</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定每个经纬度之间的采样粒度。</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 corridor 高度。</td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 corridor 拉伸高度。</td>
</tr>
<tr>
<td>cornerType</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 corridor 转角样式。<strong>ROUNDED: 0, MITERED: 1, BEVELED: 2</strong></td>
